@keyframes preloader-inside-white {
  0% {
    -webkit-transform: scale(0, 0);
    -moz-transform: scale(0, 0);
    -ms-transform: scale(0, 0);
    -o-transform: scale(0, 0);
    transform: scale(0, 0);
  }
  100% {
    -webkit-transform: scale(1, 1);
    -moz-transform: scale(1, 1);
    -ms-transform: scale(1, 1);
    -o-transform: scale(1, 1);
    transform: scale(1, 1);
  }
}

@keyframes preloader-inside-red {
  0% {
    -webkit-transform: scale(0, 0);
    -moz-transform: scale(0, 0);
    -ms-transform: scale(0, 0);
    -o-transform: scale(0, 0);
    transform: scale(0, 0);
  }
  30% {
    -webkit-transform: scale(0, 0);
    -moz-transform: scale(0, 0);
    -ms-transform: scale(0, 0);
    -o-transform: scale(0, 0);
    transform: scale(0, 0);
  }
  100% {
    -webkit-transform: scale(1, 1);
    -moz-transform: scale(1, 1);
    -ms-transform: scale(1, 1);
    -o-transform: scale(1, 1);
    transform: scale(1, 1);
  }
}

.preloader {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 10;
  padding: 20px;
}

.preloader_button {
  margin-top: 13px;
  width: 100%;
  height: 29px;
  z-index: 10;
  padding: 0px;
}

.preloader__container {
  width: 100px;
  height: 100px;
  position: relative;
}

.preloader__container_button {
  width: 29px;
  height: 29px;
}

.preloader__round {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  background: #2be080;
  border-radius: 50px;
}

.preloader__round::after,
.preloader__round::before {
  content: '';
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50px;
}

.preloader__round::after {
  background: white;
  animation: preloader-inside-white 1s ease-in-out infinite;
}

.preloader__round::before {
  z-index: 10;
  background: #2be080;
  animation: preloader-inside-red 1s ease-in-out infinite;
}

.preloader__round_button {
  position: static;
  width: 29px;
  height: 29px;
}
.preloader__round_button::after,
.preloader__round_button::before {
  content: '';
  width: 29px;
  height: 29px;
  border-radius: 50%;
}

.preloader__round_button::after {
  background: white;
  animation: preloader-inside-white 1s ease-in-out infinite;
}

.preloader__round_button::before {
  z-index: 10;
  background: #2be080;
  animation: preloader-inside-red 1s ease-in-out infinite;
}

.preloader__not-found {
  margin: 246px auto 0 auto;
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 169px;
  text-align: center;
}
